"use client"; // Required for client components

import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addTask, removeTask, toggleTask, fetchTasks } from "../redux/taskSlice";
import { AppDispatch, RootState } from "../redux/store";

export default function Home() {
  const [task, setTask] = useState<string>("");
  const tasks = useSelector((state: RootState) => state.tasks.tasks);

  const dispatch = useDispatch<AppDispatch>();

  useEffect(() => {
    dispatch(fetchTasks());
  }, [dispatch]);

  const handleAddTask = () => {
    if (task) {
      dispatch(addTask({ id: Date.now(), text: task }));
      setTask("");
    }
  };

  return (
    <div className="p-6 max-w-lg mx-auto">
      <h1 className="text-2xl font-bold mb-4">Task Manager</h1>
      <input
        className="border p-2 w-full mb-2"
        type="text"
        placeholder="Add Task"
        value={task}
        onChange={(e) => setTask(e.target.value)}
      />
      <button className="bg-blue-500 text-white px-4 py-2" onClick={handleAddTask}>
        Add Task
      </button>

      <ul className="mt-4">
        {tasks.map((t) => (
          <li key={t.id} className="flex justify-between border-b p-2">
            {t.text}
            <button className="text-red-500" onClick={() => dispatch(removeTask(t.id))}>
              Remove
            </button>
            <button 
              className={`px-2 py-1 ${t.completed ? "line-through text-gray-400" : ""}`} 
              onClick={() => dispatch(toggleTask(t.id))}
            >
              {t.text}
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
}
